body{
  margin: 0;
  padding: 0; 
}
.mainContainer{
 display: flex;
 justify-content: center;
 align-items: center;
 width: 100vw;
 height: 100vh;
 background: rgb(48,195,197);
 background: radial-gradient(circle, rgba(48,195,197,1) 0%, rgba(62,94,195,1) 100%);
}
.box{

  height: 80vh;
  width:385px;
  background-color: white;
  border-radius: 5px;
}
.enter{
  padding: 10px;
  padding-top: 1px;
}
.inputField{
  margin: 20px 0;
  width: 100%;
  display: flex;
  height: 45px;
}
.inputField input{
  width: 85%;
  height: 100%;
  outline: none;
  border-radius: 3px;
  border: 1px solid #ccc;
  font-size: 17px;
  padding-left: 15px;
  transition: all 0.3s ease;
}
.inputField input:focus{
  border-color: #8E49E8;
}
 .inputField button{
  width: 50px;
  height: 100%;
  border: none;
  color: #fff;
  margin-left: 5px;
  font-size: 21px;
  outline: none;
  background: #8E49E8;
  cursor: pointer;
  border-radius: 3px;
  opacity: 0.6;
  transition: all 0.3s ease;
}

.inputField button:hover{
  background: #5c19b4;
}
.inputField button.active{
  opacity: 1;
  pointer-events: auto;
}
.addedValue{
    position: relative;
    list-style: none;
    margin-bottom: 8px;
    background: #f2f2f2;
    border-radius: 3px;
    padding: 10px 15px;
    cursor: default;
    overflow: hidden;
    word-wrap: break-word;
}
.icon{
  position: absolute;
  right: -45px;
  top: 50%;
  transform: translateY(-50%);
  background: #e74c3c;
  width: 55px;
  text-align: center;
  color: #fff;
  padding: 10px 15px;
  border-radius: 0 3px 3px 0;
  cursor: pointer;
  transition: all 0.2s ease;
  height: 27px;
}
.fa-solid:hover{
  transform: scale(1);
}